import React from "react";
import { Button, Row, Col, Input } from "react-materialize";

class CollapseForm extends React.Component {
  state = {
    showForm: false,
  }
  trip = {

  }
  toggleForm = () => {
    let formState = this.state.showForm ? false : true;
    this.setState({showForm: formState})
  }
  addTrip = (event) => {
    event.preventDefault();
    console.log(event, this.state.trip);
  }
  render() {
    return (
      <Col offset="m2" m={8} s={12}>
        <Button className="blue-grey darken-2 light-blue-text" large onClick={this.toggleForm}>
          Add a Trip
        </Button>
        {this.state.showForm ? (
          <Row>
            <form onSubmit={this.addTrip}>
              <Input placeholder="City" s={6} label="City" />
              <Input s={6} label="State" />
              <Input
                s={12}
                label="Date"
                placeholder="xx/xx/xxxx"
              />
              <Input type="text" label="Notes" s={12} />
              <Button className="blue-grey darken-4 white-text" onClick={this.addTrip}>Add a Trip</Button>
            </form>
          </Row>
        ) : ''}
      </Col>
    );
  }
}

export default CollapseForm;



