import React from "react";
import { Row, Col, Card, Button, Icon } from "react-materialize";
import Header from "./Navbar/Header";
import { Link } from "react-router-dom";

class SingleTrip extends React.Component {
  state = {
    trip: []
  };
  componentDidMount() {
    this.getSingleTrip();
  }
  getSingleTrip = () => {
    let url = window.location.href.split("/");
    fetch(
      `https://warm-atoll-11937.herokuapp.com/api/trips/${url[url.length - 1]}`
    )
      .then(res => res.json())
      .then(trip => {
        this.setState({ trip });
      });
  };

  render() {
    return (
      <React.Fragment>
        <Header />
        <Row>
          <Col offset="m2" m={8} s={12}>
            <Card
              className="large blue-grey darken-4 white-text"
              textClassName="red-text text-lighten-2"
              title={[
                <div className="white-text">
                  {`${this.state.trip.city}, ${this.state.trip.state}`}
                </div>
              ]}
              actions={[
                <Button className="red white-text" waves="light" onClick={this.deleteTrip}>
                  <Icon right>clear</Icon>
                    Delete Trip
                </Button>,
                <Button className="light-blue" waves="light">
                  <Icon right>create</Icon>
                    Update Trip
                </Button>
              ]}
            >
              {this.state.trip.date ? this.state.trip.date.split("T")[0] : ""}
            </Card>
          </Col>
        </Row>
      </React.Fragment>
    );
  }
}

export default SingleTrip;



